import { Controller, TextPoint } from '@ali/4ever-cangjie';
import removeMention from '../commands/removeMention';

export default function onSelect(
  event: KeyboardEvent,
  controller: Controller,
  next: () => void
) {
  const { value } = controller;
  const { selection, decorations } = value;
  const mentionDecorations = decorations.find(
    item => item.mark.type === 'mention',
  );
  const { isCollapsed } = selection;
  const focus = selection.focus as TextPoint;
  if (!isCollapsed) {
    removeMention(controller);
  }
  if (isCollapsed && mentionDecorations) {
    if (
      focus.offset > mentionDecorations.start.offset ||
      focus.offset < mentionDecorations.end.offset
    ) {
      removeMention(controller);
    }
  }
  return next();
}
